@import url(https://fonts.googleapis.com/css2?family=Petit+Formal+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Petit+Formal+Script&family=Roboto:wght@400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.card{display:flex;justify-content:center;align-items:center;max-width:160px;height:240px;background:linear-gradient(100deg, #f8bbd0, #ec407a);border-radius:5px;margin:24px 24px 24px 24px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.divCard{max-width:100%;display:flex;justify-content:center;align-items:center}.title{border:1px solid #ec407a;background-color:#002171;padding:8px;color:#fff;border-radius:4px}

body{font-family:'Petit Formal Script', cursive;font-family:'Roboto', sans-serif}.tableCell{display:flex;justify-content:space-between}.header{width:100%;height:160px;background-color:#64b5f6;position:fixed}.marginTop{margin-top:160px}.headerRow{width:100%;height:160px;display:flex;justify-content:space-around;align-items:center}.headerLeft{display:flex;justify-content:end;align-items:center;font-family:'Petit Formal Script', cursive;color:#fff;margin-left:80px}.headerRight{display:flex;justify-content:end;align-items:center}.iconBook{width:80px;height:80px;margin:0 0 24px 24px}


.footer{display:flex;justify-content:space-around;align-items:center;width:100%;height:80px;font-family:"Roboto", sans-serif;background-color:#f5f5f7;font-size:10px;margin:4px}.rgb{color:#515154}.ancor{text-decoration:none;color:#1d1d1f}

